<template>
  <div class="music2">
    <div class="container">
      <div
        class="audioplayer-tobe2 skin-wave skin-wave-mode-bigwavo button-aspect-noir"
        data-playerid="3232322"
        data-type="audio"
        data-source="static/muisicplay/sounds/adg3.mp3"
      ></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "music2",
  props: {
    msg: String
  },
  mounted() {
    this.playMusicfun();
  },
  methods: {
    playMusicfun() {
      dzsap_init(".audioplayer-tobe2", {
        autoplay: "off",
        skinwave_mode: "normal",
        pcm_data_try_to_generate_wait_for_real_pcm: "on", // -- light or full
        skinwave_wave_mode: "canvas", // --- "normal" or "canvas"
        skinwave_wave_mode_canvas_mode: "normal", // --- "normal" or "reflecto"
        skinwave_wave_mode_canvas_waves_number: "3", // --- the number of waves in the canvas
        skinwave_wave_mode_canvas_waves_padding: "1", // --- padding between waves
        skinwave_wave_mode_canvas_reflection_size: "0.25", // --- the reflection size
        pcm_data_try_to_generate: "on", // --- try to find out the pcm data and sent it via ajax ( maybe send it via php_handler
        //            ,sample_time_start: '30' // --- if this is a sample to a complete song, you can write here start times, if not, leave to 0.
        //            ,sample_time_end: '87'
        //            ,sample_time_total: '120'
        settings_backup_type: "light", // == light or full
        embed_code: "light", // -- light or full
        preview_on_hover: "off", // -- light or full
        skinwave_comments_enable: "on", // -- enable the comments, ../inc/php/publisher.php must be in the same folder as this html, also if you want the comments to automatically be taken from the database remember to set skinwave_comments_retrievefromajax to ON
        skinwave_comments_retrievefromajax: "on", // --- retrieve the comment form ajax
        failsafe_repair_media_element: 500, // == light or full
        wavesurfer_pcm_length: 1024, // == light or full
        design_wave_color_bg: "444444", // == light or full
        design_wave_color_progress: "fa21da,fa213f", // == light or full
        settings_extrahtml_in_float_right:
          '<div class="player-but dzstooltip-con" style=";"><span class="the-icon-bg"></span><span class="dzstooltip arrow-from-start transition-slidein arrow-bottom skin-black align-right" style="width: auto; white-space: nowrap;">Add to Cart</span><i class="fa fa-shopping-cart svg-icon"></i></div><div class="player-but dzstooltip-con" style=";"><span class="dzstooltip arrow-from-start transition-slidein arrow-bottom skin-black align-right" style="width: auto; white-space: nowrap;">Download</span><span class="the-icon-bg"></span><i class="svg-icon fa fa-download"></i></div>  <a onclick=\'window.dzs_open_social_link("http://www.facebook.com/sharer.php?u={{replaceurl}}&caption=Santigold&description=Disparate+Youth&picture=http%3A%2F%2Flocalhost%3A8888%2Fwordpress%2Fwp-content%2Fuploads%2F2017%2F05%2Faudio_image_46.jpg",this); return false;\' class="player-but sharer-dzsap-but"><div class="the-icon-bg"></div>{{svg_share_icon}}</a>'
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container {
  width: 80%;
}
</style>



