<template>
  <div class="music1">
    <div class="container">
      <div
        class="audioplayer-tobe1 skin-wave auto-init"
        data-type="audio"
        data-source="static/muisicplay/sounds/steph1.mp3"
      ></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "music1",
  props: {
    msg: String
  },
  mounted() {
    this.playMusicfun();
  },
  methods: {
    playMusicfun() {
      dzsap_init(".audioplayer-tobe1", {
        cueFirstMedia: "on",
        autoplay: "off",
        autoplayNext: "on",
        design_menu_position: "bottom",
        enable_easing: "on",
        playlistTransition: "fade",
        mode_normal_video_mode: "one",
        design_menu_height: "200",
        skinwave_comments_enable: "on",
        skinwave_comments_retrievefromajax: "on",
        pcm_data_try_to_generate: "on",
        pcm_data_try_to_generate_wait_for_real_pcm: "on",
        skinwave_wave_mode_canvas_waves_number: 1,
        skinwave_wave_mode_canvas_waves_padding: 0,
        skinwave_wave_mode_canvas_reflection_size: 0.25,
        design_color_bg: "444444",
        design_color_highlight: "aa4444"
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container {
  width: 80%;
}
a {
  color: #42b983;
}
</style>



